<template>
  <div class="pa-5">
    <h2 class="mt-5 mb-5">i mt khu</h2>
    <v-row>
      <v-col cols="4" sm="6" md="12">
        <label>Nhp mt khu c</label>
        <v-text-field
          v-model="oldPassword"
          placeholder="Mt khu c"
          outlined
          dense
          class="mt-4"
          :error-messages="error"
          type="password"
        ></v-text-field>
      </v-col>
      <v-col cols="4" sm="6" md="12">
        <label>Mt khu mi</label>
        <v-text-field
          v-model="newPass"
          placeholder="Mt khu mi"
          outlined
          dense
          class="mt-4"
          type="password"
          :error-messages="error2"
        ></v-text-field>
      </v-col>
      <v-col cols="4" sm="6" md="12">
        <label>Nhp li mt khu</label>
        <v-text-field
          v-model="newUpdate"
          placeholder="Nhp li mt khu"
          outlined
          type="password"
          dense
          class="mt-4"
          :error-messages="error2"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-btn
      class="form__edit--submit"
      @click="checkOldPasword"
      depressed
      color="primary"
    >
      <v-icon>mdi-check</v-icon>
      Lu thay i
    </v-btn>
    <div v-if="loading" id="loader-wrapper">
      <div id="loader"></div>
    </div>
  </div>
</template>

<script>
import * as password from "../../../api/user/password";
export default {
  data() {
    return {
      loading: false,
      oldPassword: "",
      error: null,
      error2: null,
      newUpdate: "",
      newPass: "",
    };
  },
  methods: {
    async checkOldPasword() {
      var resultCheck = await password.changePassword(
        sessionStorage.getItem("IdUser"),
        this.oldPassword
      );
      if (!resultCheck) {
        this.error = "Mt khu c khng chnh xc";
      } else {
        this.error = null;
        if (this.checkNewPassword()) {
          this.error2 = null;
          this.updatePassword();
        } else {
          this.error2 = "Mt khu khng trng khp";
          return false;
        }
      }
    },
    checkNewPassword() {
      if (this.newUpdate != this.newPass) {
        return false;
      } else {
        this.error2 = null;
        return true;
      }
    },
    async updatePassword() {
      if (this.checkOldPasword) {
        this.loading = true;
        var updatePass = await password.updatePassword(
          sessionStorage.getItem("IdUser"),
          this.newPass
        );
        this.loading = false;
        if (updatePass) {
          alert("i mt khu thnh cng . Vui lng ng nhp li");
          sessionStorage.clear();
          sessionStorage.setItem("loginStatus", 0);
          this.$router.push({ path: "/login" });
        }
      } else {
        alert("C li xy ra");
      }
    },
  },
};
</script>

