<template>
  <div>
    <v-container class="user pa-5" fluid>
      <v-row class="mr-0">
        <v-col
          class="d-flex flex-column align-center border pd-0"
          cols="4"
          sm="6"
          md="3"
        >
          <div class="pa-2">
            <h2 class="text-center">Thng tin user</h2>
          </div>

          <div
            class="d-flex flex-column align-center box__avatar pd-0"
            cols="12"
            sm="6"
            md="3"
          >
            <v-avatar color="primary" size="56"></v-avatar>
            <div class="user_permission d-flex flex-column align-center">
              <p class="text-center">{{ role }}</p>
            </div>
          </div>
          <div class="user_contact d-flex flex-column">
            <p>
              <v-icon>mdi-map-marker-multiple</v-icon>
              {{ this.infoUser["a ch"] }}
            </p>
            <p>
              <v-icon>mdi-account-eye</v-icon> {{ this.infoUser["UserName"] }}
            </p>
            <hr />
            <p class="font-weight mt-2">Thng tin ti khon ngn hng</p>
            <p>
              <v-icon>mdi-bank</v-icon>
              {{ this.infoUser["Ngn hng"] }}
            </p>
            <p>
              <v-icon>mdi-account</v-icon> {{ this.infoUser["Ch ti khon"] }}
            </p>
            <p>
              <v-icon>mdi-bank-transfer</v-icon>
              {{ this.infoUser["S ti khon"] }}
            </p>
          </div>
        </v-col>
        <v-col class="usr_info" cols="4" sm="6" md="9">
          <v-simple-table>
            <template v-slot:default>
              <tbody>
                <tr v-for="(info, property) in infoUser" :key="property">
                  <td>{{ property }}</td>
                  <td>{{ info }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <v-dialog v-model="editInfoUser" width="1200">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                tile
                color="success"
                class="mt-5"
                dark
                v-bind="attrs"
                v-on="on"
              >
                <v-icon left> mdi-pencil </v-icon>
                Sa thng tin ti khon
              </v-btn>
            </template>
            <EditUserInfo :infoUser="infoUser" v-if="modalUserInfo" />
          </v-dialog>
          <v-dialog width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                tile
                color="primary"
                class="mt-5 ml-5"
                dark
                v-bind="attrs"
                v-on="on"
              >
                <v-icon left> mdi-pencil </v-icon>
                i mt khu
              </v-btn>
            </template>
            <v-card>
              <EditPassWord />
            </v-card>
          </v-dialog>
        </v-col>
      </v-row>
    </v-container>
    <div v-if="loading" id="loader-wrapper">
      <div id="loader"></div>
    </div>
  </div>
</template>

<script>
import * as user from "../../api/user/getInforUser";
import EditUserInfo from "./EditUser/EditUserInfor.vue";
import EditPassWord from "./EditUser/EditPassword.vue";
import * as convertId from "../../function/converIdUser";
import * as permission from "../../permission/checkPermission";
export default {
  name: "About",
  components: {
    EditUserInfo,
    EditPassWord,
  },

  data: () => ({
    editInfoUser: "",
    editPassword: "",
    loading: false,
    modalUserInfo: true,
    role: "",
    infoUser: {
      "M User": "",
      UserName: "",
      "H v tn": "",
      "Ngy sinh": "",
      "Gii tnh": "",
      "a ch": "",
      "Ch ti khon": "",
      "S ti khon": "",
      "Ngn hng": "",
      "Quyn ti khon": "",
    },
  }),
  async mounted() {
    this.getUserInfo();
    const role = await permission.checkPermission();
    console.log(role);
    if (role == 0) {
      this.role = "Admin";
    } else {
      this.role = "user";
    }
  },
  methods: {
    async getUserInfo() {
      this.loading = true;
      const response = await user.getInfoUser(sessionStorage.getItem("IdUser"));
      this.infoUser["H v tn"] = response.data.name;
      this.infoUser["UserName"] = response.data.username;
      this.infoUser["Ngy sinh"] = response.data.birthday;
      this.infoUser["Gii tnh"] = response.data.sex;
      this.infoUser["a ch"] =
        response.data.sub_district +
        " - " +
        response.data.district +
        " - " +
        response.data.city;
      this.infoUser["Ch ti khon"] = response.data.bank_account_name;
      this.infoUser["S ti khon"] = response.data.bank_account_number;
      this.infoUser["Ngn hng"] = response.data.bank_name;
      this.userId = convertId.convertId(sessionStorage.getItem("IdUser"));
      this.infoUser["M User"] = this.userId;
      this.infoUser["Quyn ti khon"] = response.data.role;
      this.loading = false;
    },
  },
};
</script>
<style lang="scss">
@import "./About";
.border {
  box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
  border-radius: 5px;
}
</style>
