<template>
  <div class="pa-5">
    <v-text-field
      v-model="partnerEdit.name"
      class="pl-2"
      disabled
    ></v-text-field>
    <v-text-field
      v-model="partnerEdit.link"
      solo
      label="ng dn"
      clearable
    ></v-text-field>
    <v-text-field
      v-model="partnerEdit.unit_price"
      solo
      label="Hoa hng (n v VN)"
      clearable
    ></v-text-field>
    <v-text-field
      v-model="partnerEdit.sign"
      solo
      label="K hiu"
      clearable
    ></v-text-field>
    <v-btn class="mt-2" @click="updatePartner()" color="success">
      <v-icon> mdi-file-check</v-icon> Lu thay i</v-btn
    >
    <v-btn class="mt-2 ml-5" @click="deletePartner()" color="error">
      <v-icon> mdi-file-check</v-icon> Xa i tc</v-btn
    >
    <div>
      <v-dialog v-model="confirmDelete" persistent max-width="290">
        <v-card>
          <v-card-title class="text-h5"> Xc nhn xa </v-card-title>
          <v-card-text
            >Xa i tc c th nh hng n kt qu thu nhp v hoa hng
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="acceptDelete()">
              Xc nhn xa
            </v-btn>
            <v-btn color="green darken-1" text @click="confirmDelete = false">
              Hy b
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
    <div class="mt-5">
      <v-alert v-if="alertSuccess" dense text type="success">
        Xa thnh cng
      </v-alert>
      <v-alert
        v-if="errorSuccess"
        border="right"
        colored-border
        type="error"
        elevation="2"
      >
        C li xy ra . Vui lng th li
      </v-alert>
    </div>
    <div v-if="loading" id="loader-wrapper">
      <div id="loader"></div>
    </div>
  </div>
</template>
<script>
import * as partner from "../../../api/partner/partner";
export default {
  props: ["partnerEdit"],
  data() {
    return {
      confirmDelete: false,
      alertSuccess: false,
      loading : false,
      errorSuccess: false,
    };
  },
  mounted() {},
  methods: {
    async updatePartner() {
      const update = await partner.updatePartner(
        this.partnerEdit.name,
        this.partnerEdit.link,
        this.partnerEdit.unit_price,
        this.partnerEdit.sign
      );
      console.log(update);
      alert("update thnh cng");
      location.reload();
    },
    deletePartner() {
      this.confirmDelete = !this.confirmDelete;
    },
    async acceptDelete() {
      this.loading = true;
      const deleteRes = await partner.deletePartner(this.partnerEdit.name);
      this.loading = false;
      this.confirmDelete = false;
      if (deleteRes) {
        this.alertSuccess = true;
        this.errorSuccess = false;
      } else {
        this.alertSuccess = false;
        this.errorSuccess = true;
      }
    },
  },
};
</script>