<template>
  <v-card>
    <v-card-title class="text-h5 grey lighten-2">
      Sa thng tin ti khon
    </v-card-title>
    <v-container>
      <v-row>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['M User']"
            label="M user"
            clearable
            disabled
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['H v tn']"
            label="H v tn"
            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
