            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['Gii tnh']"
            label="Gii tnh"
            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['a ch']"
            label="a ch"
            clearable
          ></v-text-field>
        </v-col>
      </v-row>
      <h2 class="mt-5">Thng tin ti khon ngn hng</h2>
      <v-row class="mt-5">
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['Ch ti khon']"
            label="Ch ti khon"
            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['S ti khon']"
            label="S ti khon"
            clearable
          ></v-text-field>
        </v-col>
        <v-col cols="4" sm="6" md="4">
          <v-text-field
            v-model="infoUser['Ngn hng']"
            label="Ngn hng"
            clearable
          ></v-text-field>
        </v-col>
      </v-row>
      <v-btn class="mt-2" @click="updateInfo()" color="success">
        <v-icon> mdi-file-check</v-icon> Lu thay i</v-btn
      >
    </v-container>
    <div v-if="loading" id="loader-wrapper">
      <div id="loader"></div>
    </div>
  </v-card>
