<script>
import * as user from "../../../api/user/updateInfoUser";
export default {
  props: ["infoUser"],
  data() {
    return {
      dialog: false,
      hidenInput: false,
      is: true,
      loading : false
    };
  },
  methods: {
    async updateInfo() {
      this.loading = true;
      const update = await user.changeInfo(
        sessionStorage.getItem("IdUser"),
        this.infoUser["H v tn"],
        this.infoUser["Ngy sinh"],
        this.infoUser["Gii tnh"], 
        this.infoUser["Ch ti khon"],
        this.infoUser["S ti khon"],
        this.infoUser["Ngn hng"]
      );
        this.loading = false;
      if (update) {
        alert("Update thng tin thnh cng");
      } else {
        alert("Xy ra li ! Vui lng th li");
      }
    },
  },
};
</script>
